, 177, 124);\n    border-radius: 5px;\n} \n\n#form-add-project #submit:active {\n    border: none;\n}\n\n#form-add-project input:hover, select:hover {\n    outline: 1px solid rgb(0, 255, 115);\n}\n\n#form-add-project input:focus, select:focus {\n    outline: 1px solid rgb(0, 255, 115);\n}\n\n.icon-today {\n    background: url(./icons/today.svg);\n    background-size: 25px;\n}\n.icon-upcoming {\n    background: url(./icons/upcoming.svg);\n    background-size: 25px;\n}\n.icon-highpriority {\n    background: url(./icons/high_priority.svg);\n    background-size: 25px;\n}\n\n\n#header {\n    grid-row: 1/2;\n    grid-column: 1/3;\n    display: grid;\n    grid-template-columns: 45px 1fr 45px;\n    padding: 10px;\n    background-color: rgb(0, 58, 36);\n}\n\n.header-button {\n    all: unset;\n    width: 25px;\n    height: 25px;\n    filter: invert(1);\n    background: url(./icons/icon.svg);\n    background-size: 25px;\n    \n}\n\n.header-button:hover {\n    filter: brightness(0) drop-shadow(0 0 3px red) invert(1);\n}\n\n.header-button.icon-menu {\n    align-self: center;\n    margin-left: 20px;\n}\n\n.header-title {\n    grid-column: 2/3;\n    margin-left: 20px;\n    align-self: center;\n}\n\n.header-button.icon-settings {\n    align-self: center;\n    grid-column: 3/4;\n}\n\n.icon-menu {\n    background: url(./icons/menu.svg);\n    background-size: 25px;\n}\n.icon-settings {\n    background: url(./icons/settings.svg);\n    background-size: 25px;\n}\n\n#mainview {\n    grid-row: 2/3;\n    grid-column: 2/3;\n    padding: 20px;\n    background-color: rgb(0, 80, 56);\n    display: grid;\n    grid-template-rows: 130px 1fr;\n}\n\n#mainview form {\n    grid-row: 2/3;\n    display: grid;\n    gap: 2px;\n    grid-template-columns: minmax(80px,1fr) 120px 100px 90px 90px;\n}\n\n#add-todo-box {\n    display: grid;\n    grid-template-rows: 30px 30px;\n    grid-row: 1/2;\n}\n\n#add-todo-box .title {\n    grid-row: 1/2;\n    font-style: italic;\n}\n\n#form-add-todo select {\n    background-color: rgba(0, 61, 43, 0.637);\n    color: white;\n    border: 2px inset rgb(0, 177, 124);\n    border-radius: 5px;\n    height: 36px;\n}\n#form-add-todo input {\n    background-color: rgba(0, 61, 43, 0.637);\n    color: white;\n    border: 2px inset rgb(0, 177, 124);\n    border-radius: 5px;\n    height: 30px;\n}\n\n#form-add-todo input:hover, select:hover {\n    outline: 1px solid rgb(0, 255, 115);\n}\n\n#form-add-todo input:focus, select:focus {\n    outline: 1px solid rgb(0, 255, 115);\n}\n\n#form-add-todo #submit {\n    height: 36px;\n}\n\n#form-add-todo #submit:active {\n    border: none;\n}\n\n#form-add-todo #desc {\n    grid-column: 1/6;\n}\n\n#todoview {\n    grid-row: 2/3;\n    display: grid;\n    grid-template-rows: 25px 1fr;\n}\n\n#todoview .labels {\n    display: grid;\n    grid-template-columns: minmax(170px,1fr) 125px 90px 70px;\n    font-style: italic;\n    margin-bottom: 10px;\n    padding: 0 10px;\n    grid-row: 1/2;\n}\n\n#todobox {\n    grid-row: 2/3;\n    height: 800px;\n    overflow: auto;\n}\n\n.todo-item {\n    display: grid;\n    grid-template-columns: minmax(170px,1fr) 125px 90px 70px;\n    grid-template-rows: 25px 5px;\n    margin-bottom: 10px;\n    border: 2px solid rgb(2, 160, 62);\n    border-radius: 10px;\n    padding: 10px;\n    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.295);\n    background-color: rgb(1, 65, 46);\n    user-select: none;\n}\n\n.todo-item * {\n    user-select: text;\n}\n\n.todo-item.completed {\n    border: 2px solid rgba(128, 128, 128, 0);\n    background-color: rgb(29, 54, 47);\n    box-shadow: none;\n    filter: opacity(50%);\n}\n\n.todo-description {\n    overflow: hidden;\n    visibility: visible;\n    height: 60px;\n    padding-top: 10px;\n    transition: 0.15s;\n    font-size: 12px;\n    grid-column: 1/5;\n}\n\n.todo-description.hidden {\n    visibility: hidden;\n    padding-top: 0;\n    height: 0px;\n    transition: 0.15s;\n}\n\n.todo-name {\n    font-size: 16px;\n    text-align: left;\n    align-self: center;\n    margin-right: auto;\n}\n\n.todo-duedate {\n    font-size: 14px;\n    align-self: center;\n    text-align: left;\n    margin-right: auto;\n}\n\n.todo-priority {\n    text-align: left;\n    align-self: center;\n    margin-right: auto;\n}\n\n.todo-actions {\n    display: grid;\n    grid-template-columns: repeat(3, 40px);\n}\n\n.todo-actions button {\n    border: none;\n    width: 25px;\n    height: 25px;\n    filter: brightness(0) invert(1);\n}\n\n.todo-actions button:hover {\n    filter: drop-shadow(0 0 3px red) invert(1);\n}\n\n.todo-project {\n    grid-column: 1/2;\n    grid-row: 2/3;\n    font-size: 11px;\n    color: rgb(162, 255, 162);\n    margin-top: -3px;\n}\n\n.icon-edit {\n    background-color: blue;\n    background: url(./icons/edit.svg);\n    background-size: 25px;\n}\n.icon-checkbox {\n    background: url(./icons/checkbox_no.svg);\n    background-size: 25px;\n}\n.icon-delete {\n    background: url(./icons/delete.svg);\n    background-size: 25px;\n}\n\n.todo-item.completed .icon-checkbox {\n    background: url(./icons/checkbox_yes.svg);\n    background-size: 25px;\n}"],"sourceRoot":""}]);
